@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



#root *:focus{
  outline: none;
}
#root{
  max-width: 1000px;
  margin: 30px auto;
}
#root,p,a,input,select,textarea,button{
  font-family: 'Rubik', sans-serif;
}
#root input, textarea, select{
  padding: 5px;
}
#root label{
  margin: 5px;
}
label span{
  margin-right: 5px;
}
#calc-form-controlls-wrapper{
  margin: 30px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.detail-settings, .detail{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 0 15px;
  margin: 10px 0;
  
}
.detail{
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-align-items: stretch;
          align-items: stretch;
  padding: 0;
  
}
.detail-plan{
  padding: 30px;
  background: #fff;
  width: 400px;
  max-height: 600px;
  border: 1px solid #ddd;
}
.detail-plan svg{
  max-height: 100%;
  max-width: 100%;
}
.detail-settings{
  background-color: #fafafa;
  margin: 0;
  padding: 10px;
  border: 1px solid #ddd;
  -webkit-align-self: flex-start;
          align-self: flex-start;
}
.detail-settings label span{
  font-size: 12px;
}
.detail-settings label span b{
  color: #3582c4;
  font-size: 14px;
}

#angles{
  border: 0;
  padding: 0;
  margin-top: 20px;
}
.angle-options-wrapper{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  position: relative;
  padding: 5px;
  border: 1px solid #ddd;
  margin: 5px 0;
  min-height: 36px;
}
.angle-options-wrapper.active{
  background-color: #ececec;
}
.angle-options-wrapper label{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
}
.angle-options__send{
  position: absolute;
  right: 0;
  top: 50px;
}
.angle-options-wrapper label:not(.switch){
  -webkit-align-items: center;
          align-items: center;
  width: 94%;
}
.angle-options-type{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
#root .angle-options-type button{
  background-color: #fff;
  border: 1px solid #3582c4;
  color: #3582c4;
  cursor: pointer;
  padding: 5px;
  font-size: 12px;
}
#root .angle-options-type button[data-active=active]{
  background-color: #3582c4;
  border: 1px solid #3582c4;
  color: #fff;
}
.angle-type__linear{
  margin: 0 5px;
}

.angle-options__tabs-header{
  margin-top: 20px;
}
.angle-options__tabs-header button.active{
  background-color: #3582c4;
  border: 1px solid #3582c4;
  color: #fff;
}
.angle-options__tabs-header button[disabled]{
  opacity: .5;
  cursor: default;
}
.angle-title-wrapper{
  margin: 0;
}
.detail-settings .angle-title{
  min-width: 160px;
  display: inline-block;
  font-size: 16px;
  padding-left: 50px;
}
.angle-title::before{
  content: '';
  border: 23px solid #ddd;
  border-left: 23px solid #d63638;
  border-top: 23px solid #d63638;
  max-height: 0;
  max-width: 0;
  position: absolute;
  display: inline-block;
  top: 0;
  left: 0;
}
.angle-title.rt::before{
  border-left: 23px solid #ddd;
  border-bottom: 23px solid #ddd;
  border-right: 23px solid #d63638;
  border-top: 23px solid #d63638;
}
.angle-title.rb::before{
  border-left: 23px solid #ddd;
  border-bottom: 23px solid #d63638;
  border-right: 23px solid #d63638;
  border-top: 23px solid #ddd;
}
.angle-title.lb::before{
  border-left: 23px solid #d63638;
  border-bottom: 23px solid #d63638;
  border-right: 23px solid #ddd;
  border-top: 23px solid #ddd;
}

label input:not([type=checkbox]){
  width: calc(100% - 12px);
}
label>input[type=number]{
  max-width: 100px;
}
.switch{
  position: absolute;
  right: 0;
  top: 63px;
}

#root button{
  margin: 0 5px;
  padding: 7px 15px;
  font-size: 16px;
  background-color: #fff;
  border: 1px solid #3582c4;
  color: #3582c4;
  cursor: pointer;
}
#root button:disabled{
  opacity: 0.5;
  cursor: default;
}
#root input[type=range]{
  position: relative;
  margin: 0 35px;
  max-width: calc(100% - 70px);
}
#root input[type=range]:before{
  content: attr(min);
  position: absolute;
  left: -20px;
  top: 7px;
  font-size: 10px;
}
#root input[type=range]:after{
  content: attr(max);
  position: absolute;
  right: -20px;
  top: 7px;
  font-size: 10px;
}
#root input[type=checkbox]{
  width: 20px;
  height: 20px;
}
#root input.error{
  background-color: #d636397c;
  border: 1px solid;
}

.w100p{width: 100%;}
.w50p{width: 50%;}

#root label.mt30{
  margin-top: 30px;
}
.ml-a{
  margin-left: auto;
}
.pt10{
  padding-top: 5px;
}

.flex{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.flex-column{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

/* COLORS */
.black{color: #1d2327;}
.light-black, #root{color: #2c3338;}
.blue{color: #3582c4;}
.light-blue{color: #72aee6;}
.red{color: #d63638;}
